@font-face {
  font-family: 'MY_futur';
  src: url('./assets/font/Futura Heavy font.ttf')  format('truetype'); /* Safari, Android, iOS */
  font-display: swap;
}
@font-face {
  font-family: 'MY_futurLt';
  src: url('./assets/font/futura light bt.ttf')  format('truetype'); /* Safari, Android, iOS */
  font-display: swap;
}
@font-face {
  font-family: 'MY_futurBody';
  src: url('./assets/font/Futura Book font.ttf')  format('truetype'); /* Safari, Android, iOS */
  font-display: swap;
}



:root {
  --main-text-color: #0071BC;
}
body {
  overflow-x: hidden;

}
*{
  font-family: 'MY_futur',Arial, Helvetica, sans-serif;

}
.bg {
  height: 40vh;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--main-text-color);
  background-image: url("./assets/img/Main/Badrum_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
  background-blend-mode: hard-light;
  border-radius: 0 4em 0 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.bg_Kok {
  background-image: url("./assets/img/Main/Kitchen_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Golv {
  background-image: url("./assets/img/Main/Floor_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Dorr {
  background-image: url("./assets/img/Main/Dorr_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Terrass {
  background-image: url("./assets/img/Main/Terrass_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_VaxHouse {
  background-image: url("./assets/img/Main/Vaxhouse_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Badrum {
  background-image: url("./assets/img/Main/Badrum_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Bastu {
  background-image: url("./assets/img/Main/Bastu_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}
.bg_Epoxy {
  background-image: url("./assets/img/Main/Epoxy_Pic.webp"),
    linear-gradient(to bottom, #adb2b6, #000);
}

@media only screen and (min-width: 560px) {
  .bg {
    height: 95vh;
  }
  .bg_container_text .col{
   /* border: 3px white solid !important;
  
    backdrop-filter: blur(10px) !important;*/
    height: 50% !important;

  }
}

.bg_container_text {
  text-align: center;
  position: absolute;
  color: white;
  height: inherit;
  display: flex;
  align-items: center;
  overflow: hidden;
  transform: translateY(-25px);
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.432);
}

.bg_container_text .col{
  /*border: 0px white solid;

  backdrop-filter: blur(0px);*/
  height: 100%;
  display: grid;
  align-content: center;
}


.gallaryCardImg {
  max-height: 50vh;
  max-width: 100vw;
  /*cursor: pointer;*/
}

@media only screen and (max-width: 380px) {
  .bg_container_text {
    transform: translateY(0px);
  }
  .bg_container_text h1{
    font-size:  1.8rem;
  }
  .bg_container_text h3{
    font-size:  1.3rem;
  }
  .bg_container_text h4{
    font-size:  1.3rem;
  }

  .footer div > div > h2{
    font-size:0.8rem;
  }
  .footer div > div{
    margin-left: -15px;
  }
  .gallaryCardImg {
    max-height: 100%;
    max-width: 100%;
    /*cursor: pointer;*/
  }
}



.main-text-color {
  color: var(--main-text-color);
}

.bg_images {
  background: linear-gradient(to top, #002c45, #000);

  background-blend-mode: saturation;
  background-position: center;
  background-repeat: repeat;
  background-size: contain;
  padding-bottom: 2em;
}

.App {
  text-align: center;
}

.custom-headers > h1 {
  font-size: 2rem;
  text-align: center;
}
.custom-headers > div {
  font-size: 0.6rem;
  text-align: left;
}

.custom-headers h2 {
  font-size: 1.1rem;
  text-align: left;
}

.custom-card-img {
 
  max-height: 300px;
  object-fit: cover;
  max-width: 300px;
}

@media only screen and (max-width: 680px) {
  .custom-card-img {
 
    max-height: 250px;
    object-fit: cover;
    max-width: 250px;
  }

}

.card {
  max-width: max-content;
  margin: 1.2em;
  align-items: center;
}
.CardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallaryCard {
  margin: 0.7em;
}





.footer div {
  padding-left: 2em;
}
.footer > div {
  padding-bottom: 1.5em;
}



/* modal styles */
.backdrop {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
}
.backdrop img {
  max-width: 100vw;
  max-height: 100vh;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}
@media only screen and (max-width: 560px) {
  .backdrop img {
    margin-top: 15%;
  }
}

li {
  list-style: none !important;
}
.Fab {
  z-index: 2;
  position: fixed;
  /*    this can be used to transition a sliding effect. Try -5rem in initial state */
  left: 20px;
  bottom: 20px;
  border-radius: 100%;
  text-align: center;
  transition: opacity 0.2s ease;

  /* this allows for a smooth transition effect*/
  cursor: pointer;
  width: 50px;
  height: 50px;
  color: white;
  background-color: var(--main-text-color);
}

.pswp__img{

  width: fit-content !important;
  height: fit-content !important;
}


.galleryTitle {
  color: white;
  padding-top: 20px;
  padding-bottom: 10px;
  text-align: center;
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: var(--main-text-color);
  margin-top: 20px;
}

.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: var(--error);
}

.Project_Card_Container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.Logo{
  cursor:pointer;
}
.Logo:hover{
  transform:scale(1.05);
}


.CloseImg{
  position: absolute;
    color: white;
    font-size: 30px;
    font-family: 'arial';
    top: 0;
    right: 5%;
}



.comments{
  margin-top: 2rem;
  color: white;
  width: 75%;
  margin-bottom: 3rem;
}

.comments > h2{
  border-bottom: 2px solid white;
}

.comments> div> input{
margin-bottom: 0.2rem;

}